import "./header.css"

import React, { useState, useEffect } from 'react';

function Header() {
	const [isLogin, setIsLogin] = useState(false); // 
	useEffect(() => {
		if (sessionStorage.getItem("tokenId") === null) {
			// sessionStorage  name  key    
		} else {
			// sessionStorage  name  key    
			//   
			setIsLogin(true);
		}
	});


	return (
		<div className="Header">
			<nav className="navbar">
				<div className="navLogo" onClick={clickLogo}>
					<span className="navLogo">JOB </span>
					<span className="navLogo" id="LogoColor">HUNTER</span>
				</div>
				<ul className="navMenu">
					{/*     */}
					{isLogin ? <li><a className="menu" href="/mypage">{""}</a></li> : <li></li>}
					{isLogin ? (<li><a className="menu" onClick={() => { sessionStorage.clear() }} href="/">{''}</a></li>)
						: (<li><a className="menu" href="/login">{''}</a></li>)}
				</ul>
			</nav>
		</div>
	);
}

const clickLogo = () => {
	document.location.href = '/';
}

export default Header;